import React, { Component } from 'react';
import { Formik, FormikProps, Form, Field, ErrorMessage } from 'formik';
import {getPatientSet,getHospitalSet,updateTicketCatagory,getRoles, getTicketCatagory } from './../Apis.js'
import { Alert } from 'reactstrap';
import * as Yup from 'yup';
import {RadioGroup, Radio} from 'react-radio-group'
import { SketchPicker } from 'react-color'
import {
  Badge,
  Button,
  ButtonDropdown,
  Card,
  CardBody,
  CardFooter,
  CardHeader,
  Col,
  Collapse,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Fade,
  FormGroup,
  FormText,
  FormFeedback,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
  Row,
} from 'reactstrap';



class UpdateTicketCatagory extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name:'',
            hospital_id:'',
            status:'',
            created_by:'',
            visible:false,
            user_id:'',
            duration:'',
            color:'',
          
            showForm:false,
            msgType:'success',
            assignType:'user',
            massege:''
          
        };
        this.onDismiss = this.onDismiss.bind(this);
      }
    onDismiss() {
        this.setState({ visible: false });
      }
    handleSubmit = (values, { 
        props = this.props, 
        setSubmitting 
      }) => {
          values["color"]=this.state.color;
        if(this.state.assignType=='user')
        {
            console.log(values.user_id)
            values["user_id"]=values.user_id;
        }
        else{
            console.log('role',values.user_id)
            values["role_id"]=values.user_id;
            delete values.user_id;
        }
        console.log(values)
        this.updateTicketCatagory(values,setSubmitting,this.props.match.params.id)
       
    }

      updateTicketCatagory=async(values,setSubmitting,id)=>{
          const responseData= await updateTicketCatagory(values,id);
          console.log(responseData,'response')
          if(responseData.data.message)
          {
              this.setState({ visible:true,massege:'TicketCatagory Update Failed '+responseData.message,msgType:'danger'})
              setSubmitting(false);
          }
          else{
              this.setState({ visible:true,msgType:'success',massege:'TicketCatagory Update Successfully'})
              setSubmitting(false);
              return;
          }
      }
      handleClick = () => {
        this.setState({ displayColorPicker: !this.state.displayColorPicker })
      };
    
      handleClose = () => {
        this.setState({ displayColorPicker: false })
      };
    
      handleChange = (color, event) => {
          console.log(color)
        this.setState({ color: color.hex });
      };
      handleChangeRadio=(value)=> {
        this.setState({assignType: value});
    //    console.log(this.state.RoleSets)
        // console.log(value)
      }
      componentDidMount=async()=>{
            const responseData= await getHospitalSet();
            console.log(responseData,'response')
            if(responseData.status == 200){
                this.setState({hospitalSets:responseData.data.result})
            } else if (responseData.status == 404) {
                console.warn(responseData.data.message);
            } 
            const responseDataPatient= await getPatientSet();
            console.log(responseDataPatient,'response')
            if(responseDataPatient.status == 200){
                this.setState({patientSets:responseDataPatient.data})
            } else if (responseDataPatient.status == 404) {
                console.warn(responseDataPatient.data.message);
            } 
            const responseDataTicketCatagory= await getTicketCatagory(this.props.match.params.id);
            console.log(responseDataTicketCatagory,'response')

            const responseDataRole= await getRoles();
            console.log(responseDataRole,'response')
            if(responseDataRole.status == 200){
                this.setState({RoleSets:responseDataRole.data.result})
            } else if (responseDataRole.status == 404) {
                console.warn(responseDataRole.data.message);
            } 
            if(responseDataTicketCatagory.status == 200){
                if(responseDataTicketCatagory.data.ticket_category_assignee[0].user_id)
                {
                    this.setState({
                        assignType:'user',
                        user_id:responseDataTicketCatagory.data.ticket_category_assignee[0].user_id,
                    })
                }
                else{
                    this.setState({
                        user_id:responseDataTicketCatagory.data.ticket_category_assignee[0].role_id,
                        assignType:'role'
                    })

                }
                this.setState({
                
                    name:responseDataTicketCatagory.data.ticketCategory.name,
                    hospital_id:responseDataTicketCatagory.data.ticketCategory.hospital_id,
                    created_by:responseDataTicketCatagory.data.ticketCategory.created_by,
                    
                    // user_id:responseDataTicketCatagory.data.ticket_category_assignee[0].user_id,
                    status:responseDataTicketCatagory.data.ticketCategory.status,
                    duration:responseDataTicketCatagory.data.ticket_category_priority[0].duration,
                    color:responseDataTicketCatagory.data.ticket_category_priority[0].color,
                    showForm:true,
                    
                })
            } else if (responseDataTicketCatagory.status == 404) {
                console.warn(responseDataTicketCatagory.data.message);
            } 
            

        }
    render() {
        console.log(this.state.visible)
        const popover = {
            position: 'absolute',
            zIndex: '2',
          }
          const cover = {
            position: 'fixed',
            top: '0px',
            right: '0px',
            bottom: '0px',
            left: '0px',
          }
        return(
            <div className="animated fadeIn">
                <Row>
                <Col xs="2">
                </Col>
                    <Col xs="8">
                    <Card>
                        <CardHeader>
                            <strong>Update TicketCatagory</strong>
                        </CardHeader>
                        <CardBody> 
                            {this.state.showForm&&<Formik
                                    initialValues={{
                                        name: this.state.name,
                                        hospital_id: this.state.hospital_id,
                                        status: this.state.status,
                                        created_by: this.state.created_by,
                                     
                                        user_id: this.state.user_id,
                                        duration: this.state.duration,
                                        // color: this.state.color,
                                        modified_by: localStorage.getItem('id'),
                                    }}
                            /*validate={values => {
                                let errors = {};
                                if (!values.email) {
                                  errors.email = 'Required';
                                } else if (
                                  !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                                ) {
                                  errors.email = 'Invalid email address';
                                }
                                return errors;
                              }}*/
                            // validationSchema={Yup.object().shape({
                            // email: Yup
                            //     .string()
                            //     .email()
                            //     .required(),
                            // password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // confirm_password: Yup
                            //     .string()
                            //     .min(6)
                            //     .required(),
                            // phone:Yup
                            //     .number()
                            //     .required(),
                            // alternate_phone:Yup
                            //     .number()
                            //     .required(),
                            // })}
                            
                                    onSubmit={this.handleSubmit}
                                    render={formProps => {
                                        return(
                                        <Form className="form-horizontal">
                                       <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                            type="text" 
                                            name="name" 
                                            placeholder="Name" 
                                            id="text-input"
                                            re
                                            className="form-control"
                                            /> 
                                            <ErrorMessage name="first_name" />
                                        </Col>                                        
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Hospital Name:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                    
                                            <Field 
                                        component="select" 
                                            name="hospital_id" 
                                            placeholder="Hospital Id" 
                                            className="form-control"
                                            >
                                            {this.state.hospitalSets&&this.state.hospitalSets.map((hospital, index) =>
                                            <option key={index} value={hospital.id}>{hospital.name}</option>
                                            )}
                                            </Field> 
                                            <ErrorMessage name="short_name" />
                                        </Col>                                         
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Assignee Type:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                        <RadioGroup
                                            name="fruit"
                                            selectedValue={this.state.assignType}
                                            onChange={this.handleChangeRadio}>
                                            <label className="radioClass">
                                                <Radio value="user" /> User
                                            </label>
                                            <label>
                                                <Radio value="role" /> Role
                                            </label>
                                        </RadioGroup>
                                        </Col>
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Assignee:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                    
                                            {this.state.assignType=='user'?
                                            <Field 
                                            component="select" 
                                            name="user_id" 
                                            placeholder="User Name" 
                                            className="form-control"
                                            >
                                              {this.state.patientSets&&this.state.patientSets.map((patient, index) =>
                                             patient.role_name!='patient'&&
                                                 <option key={index} value={patient.id}>{patient.first_name+' '+patient.last_name +' ('+patient.role_name+')'}</option>
                                            )}
                                            </Field> 
                                             :   
                                            <Field 
                                            component="select" 
                                            name="user_id" 
                                            placeholder="User Name" 
                                            className="form-control"
                                            >
                                              {this.state.RoleSets&&this.state.RoleSets.map((role, index) =>
                                             
                                                 <option key={index} value={role.id}>{role.name}</option>
                                            )}
                                            
                                            </Field> 
                                            }
                                            <ErrorMessage name="short_name" />
                                        </Col>                                         
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Duration:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                            type="text" 
                                            name="duration" 
                                            placeholder="Duration" 
                                            className="form-control"
                                            /> 
                                            <ErrorMessage name="duration" />
                                        </Col>                                        
                                    </FormGroup>
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Color:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                             <div style={{   padding: '5px',
                                                backgroundColor: '#fff',
                                                borderRadius: '1px',
                                                boxShadow: '0 0 0 1px rgba(0,0,0,.1)',
                                                display: 'inline-block',
                                                cursor: 'pointer',}} onClick={ this.handleClick }> <div style={ { width: '36px',
                                                height: '14px',
                                                borderRadius: '2px',
                                                backgroundColor:this.state.color }} /></div>
                                                { this.state.displayColorPicker ? <div style={ popover }>
                                                <div style={ cover } onClick={ this.handleClose }/>
                                                <SketchPicker color={ this.state.color }  onChange={ this.handleChange } />
                                                </div> : null }
                                        </Col>                                        
                                    </FormGroup>
                                
                                 
                                    
                                    <FormGroup row>
                                        <Col md="3">
                                            <Label htmlFor="text-input">Status:</Label>
                                        </Col>
                                        <Col xs="12" md="9">
                                            <Field 
                                            component="select" 
                                            name="status" 
                                            placeholder="Status" 
                                            className="form-control"
                                           >
                                               <option  value={1}>{'Enable'}</option>
                                               <option  value={2}>{'Disable'}</option>
                                        </Field>
                                            <ErrorMessage name="alternate_phone" />
                                        </Col>                                  
                                    </FormGroup>
                                            <Col md="3" style={{float:'right'}}>
                                            <button 
                                                type="submit"
                                                className="btn btn-success btn-block custonbtn" 
                                                
                                                disabled={formProps.isSubmitting}>
                                                Update TicketCatagory
                                            </button>
                                            </Col>
                                            </Form>
                                );
                            }}

                            />}
                             <Alert color={this.state.msgType} style={{marginTop:65}}isOpen={this.state.visible} toggle={this.onDismiss}>
                                {this.state.massege}
                            </Alert>
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </div>
            
        );
    }

}

export default UpdateTicketCatagory;